/**
 * üì¶ Êï∏ÊìöÂ≠òÂÑ≤Ê®°ÁµÑ (Data Store Module)
 *
 * ÁÆ°ÁêÜÁÅ´ÁÅΩÂú∞ÈªûÊï∏ÊìöÂíåÂú∞ÂúñÈ°ØÁ§∫ÂäüËÉΩ
 * ‰ΩøÁî® Pinia ÁãÄÊÖãÁÆ°ÁêÜÁ≥ªÁµ±Âíå Vue 3 Composition API
 */

// Ê†∏ÂøÉ‰æùË≥¥
import { defineStore } from 'pinia';
import { ref, computed } from 'vue';

/**
 * üè™ Êï∏ÊìöÂ≠òÂÑ≤ÂïÜÂ∫óÂÆöÁæ© (Data Store Definition)
 *
 * ‰ΩøÁî® Pinia ÁöÑ defineStore ÂâµÂª∫‰∏ÄÂÄãÂêçÁÇ∫ 'data' ÁöÑÁãÄÊÖãÁÆ°ÁêÜÂïÜÂ∫ó„ÄÇ
 * Êé°Áî® Composition API Ë™ûÊ≥ïÔºåÊèê‰æõÊõ¥Â•ΩÁöÑ TypeScript ÊîØÊè¥Âíå‰ª£Á¢ºÁµÑÁπî„ÄÇ
 *
 * @returns {Object} ÂåÖÂê´ÊâÄÊúâÁãÄÊÖãÂíåÊñπÊ≥ïÁöÑÂïÜÂ∫óÂ∞çË±°
 */
export const useDataStore = defineStore(
  'data', // ÂïÜÂ∫óÂîØ‰∏ÄÊ®ôË≠òÁ¨¶
  () => {
    // üìç ÂÑ≤Â≠òÁöÑÂú∞ÈªûÊï∏Êìö
    const savedLocations = ref([]);
    const loading = ref(false);
    const error = ref(null);

    // üó∫Ô∏è Âú∞ÂúñÂØ¶‰æã
    const mapInstance = ref(null);
    const selectedFeature = ref(null);

    /**
     * üì• ËºâÂÖ•ÂÑ≤Â≠òÁöÑÂú∞ÈªûÊï∏Êìö
     *
     * Âæû public/data/Â∑≤ÂÑ≤Â≠òÁöÑÂú∞Èªû.json ËºâÂÖ•ÊâÄÊúâÂÑ≤Â≠òÁöÑÂú∞ÈªûÊï∏Êìö
     */
    const loadSavedLocations = async () => {
      loading.value = true;
      error.value = null;

      try {
        console.log('üìç ÈñãÂßãËºâÂÖ• JSON Êñá‰ª∂...');
        const response = await fetch('/30DayMapChallenge-15_Fire/data/Â∑≤ÂÑ≤Â≠òÁöÑÂú∞Èªû.json');
        if (!response.ok) {
          throw new Error(`HTTP error! status: ${response.status}`);
        }
        const data = await response.json();
        savedLocations.value = data.features || [];
        console.log(`üìç ÊàêÂäüËºâÂÖ• ${savedLocations.value.length} ÂÄãÂÑ≤Â≠òÁöÑÂú∞Èªû`);
      } catch (err) {
        error.value = err.message;
        console.error('‚ùå ËºâÂÖ•ÂÑ≤Â≠òÁöÑÂú∞ÈªûÊï∏ÊìöÂ§±Êïó:', err);
      } finally {
        loading.value = false;
      }
    };

    /**
     * üó∫Ô∏è Ë®≠ÂÆöÂú∞ÂúñÂØ¶‰æã
     */
    const setMapInstance = (map) => {
      mapInstance.value = map;
    };

    /**
     * üéØ Ë®≠ÂÆöÈÅ∏‰∏≠ÁöÑË¶ÅÁ¥†
     */
    const setSelectedFeature = (feature) => {
      selectedFeature.value = feature;
    };

    /**
     * üîç Ê†πÊìöÂ±¨ÊÄßÊêúÂ∞ãÂú∞Èªû
     */
    const searchLocations = (query) => {
      if (!query) return savedLocations.value;

      return savedLocations.value.filter((location) => {
        const name = location.properties?.location?.name || '';
        const address = location.properties?.location?.address || '';
        return (
          name.toLowerCase().includes(query.toLowerCase()) ||
          address.toLowerCase().includes(query.toLowerCase())
        );
      });
    };

    /**
     * üìä Áç≤ÂèñÁµ±Ë®àË≥áË®ä
     */
    const getStatistics = computed(() => {
      const total = savedLocations.value.length;
      const countries = [
        ...new Set(
          savedLocations.value.map((loc) => loc.properties?.location?.country_code || 'Unknown')
        ),
      ];

      return {
        total,
        countries: countries.length,
        countryList: countries,
      };
    });

    return {
      // ÁãÄÊÖã
      savedLocations,
      loading,
      error,
      mapInstance,
      selectedFeature,

      // ÊñπÊ≥ï
      loadSavedLocations,
      setMapInstance,
      setSelectedFeature,
      searchLocations,
      getStatistics,
    };
  },
  {
    persist: true,
  }
);
